body{background-color:#202c2e;background:url(https://nathanaelmathieu.github.io/pro-site/static/media/moonrise.2fe4f1da.JPG) no-repeat center center fixed;background-size:cover;margin:0;font-family:"Montserrat",sans-serif}body a:link{background-color:#202c2e;color:#c9c9c9;box-shadow:2px 2px calc(2px * 2) #000,calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #97b2b6;border-radius:5px;padding:2px;text-decoration:none}body a:visited{background-color:#202c2e;color:#c9c9c9;box-shadow:2px 2px calc(2px * 2) #000,calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #97b2b6}body a:active{background-color:#c9c9c9;color:#202c2e;box-shadow:inset 2px 2px calc(2px * 2) #4a4a4a,inset calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #fff}#root{height:100vh}
.Projects{background-color:rgba(32,44,46,.8);color:#c9c9c9;display:grid;grid-template-columns:1fr;grid-column-gap:2vw;-webkit-column-gap:2vw;column-gap:2vw;grid-row-gap:3vh;row-gap:3vh;padding:2%;padding-bottom:4%;justify-content:center;align-items:center}@media only screen and (min-width: 600px){.Projects{grid-template-columns:1fr 1fr}}@media only screen and (min-width: 1000px){.Projects{grid-template-columns:1fr 1fr 1fr}}
.Header{background-color:rgba(32,44,46,.6);color:#c9c9c9;display:flex;flex-direction:row;align-items:center;justify-content:flex-start;text-decoration:none;font-family:"Raleway",sans-serif;padding:10px;height:50px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;list-style-type:none}.Header a{text-decoration:none;padding:3px 5px 2px 5px;border-radius:8px;background-color:#202c2e;color:#c9c9c9;box-shadow:2px 2px calc(2px * 2) #000,calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #97b2b6}.Header a:hover{background-color:#202c2e;color:#c9c9c9;box-shadow:5px 5px calc(5px * 2) #000,calc(0px - 5px) calc(0px - 5px) calc(5px * 2) #97b2b6;cursor:pointer}.Header a:active{background-color:#c9c9c9;color:#202c2e;box-shadow:inset 5px 5px calc(5px * 2) #4a4a4a,inset calc(0px - 5px) calc(0px - 5px) calc(5px * 2) #fff;cursor:pointer}
.wrapper .ImageCard,.wrapper .ImageCard.alt-text-color,.wrapper .ImageCard:hover,.wrapper .ImageCard.Clickable,.wrapper .ImageCard.Clickable:active{background-color:#c9c9c9;color:#202c2e;box-shadow:inset 2px 2px calc(2px * 2) #4a4a4a,inset calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #fff;padding:3%;display:flex;align-items:center;border-radius:12px;transition:box-shadow .5s,-webkit-transform .5s ease-in-out;transition:box-shadow .5s,transform .5s ease-in-out;transition:box-shadow .5s,transform .5s ease-in-out,-webkit-transform .5s ease-in-out;position:relative}.wrapper .ImageCard .image{max-height:100%;max-width:100%;object-fit:contain}.wrapper .ImageCard .text{display:flex;align-items:flex-end;justify-content:flex-end;width:calc(100% - 6% - 20px);height:calc(100% - 9% - 20px);position:absolute;background:rgba(255,255,255,.2);padding:10px;text-align:right;font-size:medium;color:#202c2e;visibility:hidden;opacity:0;overflow:hidden;transition:opacity .5s,visibility .2s}.wrapper .ImageCard.Clickable,.wrapper .ImageCard.Clickable:active{background-color:#202c2e;color:#c9c9c9;box-shadow:1px 1px calc(1px * 2) #000,calc(0px - 1px) calc(0px - 1px) calc(1px * 2) #97b2b6}.wrapper .ImageCard.Clickable:hover{background-color:#202c2e;color:#c9c9c9;box-shadow:2px 2px calc(2px * 2) #000,calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #97b2b6;cursor:pointer}.wrapper .ImageCard.Clickable:active{background-color:#c9c9c9;color:#202c2e;box-shadow:inset 2px 2px calc(2px * 2) #4a4a4a,inset calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #fff;cursor:pointer}.wrapper .ImageCard.Clickable:active.alt-text-color{color:#c9c9c9}.wrapper .ImageCard:hover .text{visibility:visible;opacity:1}.wrapper .ImageCard.alt-text-color .text{color:#c9c9c9}
.Text,.heading,.small,.body{font-family:"Montserrat",sans-serif;color:#c9c9c9;border-radius:12px;margin:5px 0;padding:0 10px}.theme,.neumorphic{background-color:#202c2e;color:#c9c9c9}.transparent-theme{background-color:rgba(32,44,46,.8);color:#c9c9c9}.neumorphic{padding:0 20px 0 20px;border-radius:12px}.body{font-size:calc(1rem + 0.3vw);text-indent:2em;line-height:150%}.small{font-size:18px}.heading{font-family:"Raleway",sans-serif;text-indent:unset;font-size:30px;font-weight:1000;text-align:center;line-height:.5}
.IconButtons{background-color:#202c2e;color:#c9c9c9;padding:1vw;border-radius:12px;display:grid;grid-template-columns:repeat(4, auto);grid-gap:15%;width:300px;justify-content:center}.IconButtons a{overflow:hidden;border-radius:10px;padding:8px 30px 6px 8px;background-color:#202c2e;color:#c9c9c9;box-shadow:2px 2px calc(2px * 2) #000,calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #97b2b6}.IconButtons a div svg{color:inherit}.IconButtons a:hover{background-color:#202c2e;color:#c9c9c9;box-shadow:3px 3px calc(3px * 2) #000,calc(0px - 3px) calc(0px - 3px) calc(3px * 2) #97b2b6;cursor:pointer}.IconButtons a:active{background-color:#c9c9c9;color:#202c2e;box-shadow:inset 4px 4px calc(4px * 2) #4a4a4a,inset calc(0px - 4px) calc(0px - 4px) calc(4px * 2) #fff}.IconButtons a:active div svg{color:inherit}
.Layout{min-width:350px;scroll-behavior:smooth;height:100%}.Layout .header_container{position:-webkit-sticky;position:sticky;top:0;width:100%;z-index:99}.Layout .Body{height:calc(100% - 50px)}
.Layout body{display:flex;flex-direction:column;align-items:center}.Layout body h1{font-family:"Raleway",sans-serif}.Layout body p{font-family:"Montserrat",sans-serif}
.MainPage{display:grid;grid-template-rows:100vh 1fr;grid-gap:20vh}.MainPage #about{display:flex;flex-direction:row;align-items:center;justify-content:flex-end}.MainPage #about #contact{padding:calc(5vw - 9px);display:grid;grid-template-rows:repeat(3, auto);grid-gap:8px;min-width:340px;width:35vw;justify-items:center}.MainPage #projects{display:flex;flex-direction:column;align-items:center}.MainPage #projects :first-child{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}
